import React, { useState } from "react";
import "./ResponsiveForm.css";
import RT from "./ResponsiveTable";
import Module from "./Modules";
const Forms = () => {
  const [ner, sner] = useState("");
  const [eer, seer] = useState("");
  const [per, sper] = useState("");
  const validation = (e, type) => {
    e.preventDefault();
    const val = e.target.value;
    if (type === "name") {
      if (val.trim() === "") {
        sner("name required");
      } else if (val.match(/[a-zA-z]/g)) {
        sner("");
      } else {
        sner("Letter reuired");
      }
    } else if (type === "email") {
      if (val.trim() === "") {
        seer("email required");
      } else if (val.indexOf("@")) {
        seer("Please add @");
      }
    } else if (type === "password") {
      if (val.trim() === "") {
        sper("password required");
      } else if (val.match(/[a-zA-Z1-9@#$%^&*_]/g)) {
        sper("Please add @");
      }
    }
  };
  return (
    <div>
      <div className="resForm">
        <div className="resFormGroup">
          <label>Name</label>
          {ner ? <span className="text-danger mt-2">{ner}</span> : ""}
          <input
            type="text"
            placeholder="Please enter name"
            onChange={(e) => validation(e, "name")}
          />
        </div>
        <div className="resFormGroup">
          <label>Email</label>
          {eer ? <span className="text-danger">{eer}</span> : ""}
          <input
            type="email"
            placeholder="Please enter email"
            onChange={(e) => validation(e, "email")}
          />
        </div>
        <div className="resFormGroup">
          <label>Password</label>
          {per ? <span className="text-danger">{per}</span> : ""}
          <input
            type="password"
            placeholder="Please enter password"
            onChange={(e) => validation(e, "password")}
          />
        </div>
        <button className="mt-2">Submit</button>
      </div>
      <RT />
      <Module />
    </div>
  );
};

export default Forms;
