import { useEffect, useState } from "react";

const RT = () => {
  const [data, setData] = useState();
  useEffect(() => {
    (async () => {
      const url = "https://jsonplaceholder.typicode.com/users";
      const res = await fetch(url);
      const dt = await res.json();
      setData(dt);
    })();
  }, []);

  return (
    <table>
      <thead>
        <tr>
          <th> ID</th>
          <th> Name</th>
          <th> Email</th>
          <th> Password</th>
          <th> Role</th>
        </tr>
      </thead>
      <tbody>
        {data &&
          data.map((i, ind) => {
            return (
              <tr>
                <td>{i["id"]}</td>
                <td>{i["name"]}</td>
                <td>{i["email"]}</td>
                <td>{i["username"]}</td>
                <td>{i["id"] > 4 ? "Admin" : "Cus"}</td>
              </tr>
            );
          })}
      </tbody>
    </table>
  );
};
export default RT;
